/* General Styles */
.admin-login-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.admin-login-container h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.5em;
}

.error-message {
  color: red;
  font-size: 0.9em;
  text-align: center;
  margin-bottom: 10px;
}

.admin-login-container form {
  display: flex;
  flex-direction: column;
}

.admin-login-container label {
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 1em;
}

.admin-login-container input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 5px;
  font-size: 1em;
}

.admin-login-container button {
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 4px;
  font-size: 1em;
}

.admin-login-container button:hover {
  background-color: #0056b3;
}

/* Mobile Responsiveness */
@media (max-width: 480px) {
  .admin-login-container {
    margin: 20px auto;
    padding: 15px;
    width: 200px;
  }

  .admin-login-container h2 {
    font-size: 1.3em;
    margin-bottom: 15px;
  }

  .error-message {
    font-size: 0.85em;
    margin-bottom: 8px;
  }

  .admin-login-container label {
    font-size: 0.95em;
  }

  .admin-login-container input {
    padding: 10px;
    font-size: 1em;
  }

  .admin-login-container button {
    padding: 12px;
    font-size: 1em;
  }
}
