/* Container and Title Styling */
.admin-doctors-container {
  width: 100vw;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.admin-doctors-title {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.error-message {
  color: #f44336;
  text-align: center;
}

/* Card Container */
.doctor-cards-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

/* Card Styling */
.doctor-card {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  max-width: 280px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.doctor-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.doctor-card h3 {
  font-size: 1.2rem;
  color: #333;
}

.doctor-card p {
  color: #666;
  margin: 5px 0;
  font-size: 1rem;
}

/* Tablet View */
@media (max-width: 768px) {
  .admin-doctors-title {
    font-size: 1.8rem;
    margin-bottom: 15px;
  }

  .doctor-cards-container {
    flex-direction: column;
    align-items: center;
  }

  .doctor-card {
    max-width: 90%;
    width: 300px;
    padding: 20px;
    /* font-size: 30px; */
  }

  .doctor-card h3 {
    font-size: 0.8rem;
  }

  .doctor-card p {
    font-size: 1rem;
  }
}

/* Mobile View */
@media (max-width: 480px) {
  .admin-doctors-container {
    /* padding: 15px; */
    width: 100vw;
  }

  .admin-doctors-title {
    font-size: 1rem;
    margin-bottom: 10px;
  }

  .doctor-card {
    /* max-width: 100%; */
    padding: 15px;
    width: 200px;
  }

  .doctor-card h3 {
    font-size: 1rem;
  }

  .doctor-card p {
    font-size: 0.85rem;
  }
}
