/* Container */
.medicine-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

/* Medicine List */
.medicine-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.medicine-item {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  width: 300px;
  transition: transform 0.2s;
}

.medicine-item:hover {
  transform: translateY(-5px);
}

.medicine-item > h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  font-size: 1.2rem;
}

/* Buttons */
button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 25px;
  transition: background-color 0.3s ease-in-out;
}

button:hover {
  background-color: #0056b3;
}

/* Quantity Controls */
.quantity-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.quantity-controls button {
  padding: 5px 10px;
  cursor: pointer;
}

/* Search Input */
.search {
  width: 100%;
  max-width: 400px;
  margin-bottom: 30px;
  padding: 12px;
  font-size: 16px;
  border: 2px solid #1976d2;
  border-radius: 25px;
  outline: none;
  transition: all 0.3s ease-in-out;
}

.search:focus {
  border-color: #1e88e5;
  box-shadow: 0 0 10px rgba(0, 135, 253, 0.5);
}

.search::placeholder {
  color: #999;
  font-style: italic;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .medicine-item {
    width: 90%;
    padding: 15px;
  }

  .medicine-item > h2 {
    font-size: 1.1rem;
  }

  .quantity-controls {
    gap: 5px;
  }

  .quantity-controls button {
    padding: 5px 8px;
    font-size: 0.9rem;
  }

  .search {
    max-width: 90%;
    font-size: 14px;
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .medicine-container {
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  .medicine-container h1 {
    font-size: 1rem;
  }

  .medicine-item {
    width: 200px;
    padding: 12px;
  }

  .medicine-item > h2 {
    font-size: 1rem;
  }

  button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }

  .search {
    max-width: 150px;
    font-size: 14px;
    padding: 8px;
  }
}
